<template>
  <div id="app">
    <widget :cardsCount="cardsCount" :tabsheetsCount="tabsheetsCount" />
  </div>
</template>

<script>
import widget from '@/components/widget.vue'

export default {
  components: {
    widget,
  },
  data() {
    return {
      width: document.body.clientWidth,
    }
  },
  created() {
    window.addEventListener('resize', this.updateWidth)
  },
  computed: {
    cardsCount: function() {
      if (this.width >= 720) {
        return 4
      } else {
        return 2
      }
    },
    tabsheetsCount: function() {
      if (this.width >= 720) {
        return 7
      } else {
        return 3
      }
    },
  },
  methods: {
    updateWidth() {
      this.width = document.body.clientWidth
    },
  },
}
</script>
