<template>
  <div class="widget">
    <div class="currencies">
      <span>Курс {{ baseCurrency }} сегодня</span>
      <div class="nav">
        <svg
          width="7"
          height="12"
          viewBox="0 0 7 12"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
          @click="previousPair"
          v-show="move > 0"
        >
          <path
            d="M7 10.58L2.9925 6L7 1.41L5.76625 0L0.51625 6L5.76625 12L7 10.58Z"
          />
        </svg>
        <div class="tabsheets">
          <button
            v-for="(pair, index) in page"
            :key="index"
            @click="changePair(index)"
            :class="{ active: pair.selected }"
          >
            {{ pair }}
          </button>
        </div>
        <svg
          width="7"
          height="12"
          viewBox="0 0 7 12"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
          @click="nextPair"
          v-show="move < currenciesList.length - tabsheetsCount"
        >
          <path
            d="M0.516357 10.58L4.52386 6L0.516357 1.41L1.75011 0L7.00011 6L1.75011 12L0.516357 10.58Z"
          />
        </svg>
      </div>
    </div>
    <div class="info">
      <div class="input-window">
        <input type="number" v-model="currencyCount" />
        <span>{{ baseCurrency }}</span>
      </div>
      <div class="result-window" v-if="currencyCount != ''">
        <div class="card" v-for="(res, index) in pagination" :key="index">
          <div class="count">
            <span>{{ currencyCount }}</span>
            <span> {{ baseCurrency }} =</span>
          </div>
          <div class="result">
            <span>{{ res.count }}</span>
            <span> {{ res.currency }}</span>
          </div>
        </div>
      </div>
      <div class="paginator" v-if="currencyCount != ''">
        <button @click="previousPage" :class="{ disabled: isDisabled.left }">
          <svg
            width="7"
            height="12"
            viewBox="0 0 7 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M7 10.58L2.9925 6L7 1.41L5.76625 0L0.51625 6L5.76625 12L7 10.58Z"
            />
          </svg>
          Назад
        </button>
        <button @click="nextPage" :class="{ disabled: isDisabled.right }">
          <svg
            width="7"
            height="12"
            viewBox="0 0 7 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M0.516357 10.58L4.52386 6L0.516357 1.41L1.75011 0L7.00011 6L1.75011 12L0.516357 10.58Z"
            />
          </svg>
          Далее
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'

export default {
  data() {
    return {
      baseCurrency: 'EUR',
      currencyCount: 1,
      currenciesList: [],
      currencyPairs: [],
      move: 0,
      paginationBegin: 0,
      isDisabled: {
        left: true,
        right: false,
      },
    }
  },
  props: ['cardsCount', 'tabsheetsCount'],
  created() {
    this.fetchData()
  },
  computed: {
    page: function() {
      return this.currenciesList.slice(
        this.move,
        this.move + this.tabsheetsCount
      )
    },
    pagination: function() {
      for (let i = 0; i < this.currencyPairs.length; i++) {
        if (this.currencyPairs[i].currency === this.baseCurrency)
          this.currencyPairs.splice(i, 1)
      }

      if (this.currencyCount != '') {
        this.currencyPairs.forEach(element => {
          element.count = (element.value * this.currencyCount).toFixed(2)
          element.count = element.count.replace('.', ',')
          element.count = this.addSeparator(element.count)
        })
      }
      return this.currencyPairs.slice(
        this.paginationBegin,
        this.paginationBegin + this.cardsCount
      )
    },
  },
  methods: {
    fetchData() {
      Axios.get(
        'https://cors-anywhere.herokuapp.com/https://api.openrates.io/latest?base=' +
          this.baseCurrency
      ).then(response => {
        this.currencyPairs = []
        this.currenciesList = []
        this.getCurrenciesList(response.data.rates)
      })
    },
    getCurrenciesList(payload) {
      Object.entries(payload).forEach(element => {
        this.currenciesList.push(element[0])
        this.currencyPairs.push({
          currency: element[0],
          value: element[1],
          count: '',
          selected: false,
        })
      })
    },
    nextPair() {
      this.move += 1
    },
    previousPair() {
      this.move -= 1
    },
    changePair(index) {
      this.currencyPairs.forEach(element => {
        element.selected = false
      })
      if (this.move + index > this.currencyPairs.length)
        this.currencyPairs[this.move + index - 1].selected = true
      if (this.move + index < 0)
        this.currencyPairs[this.move + index + 1].selected = true
      this.baseCurrency = this.currenciesList[index + this.move]
      this.fetchData()
    },
    nextPage(e) {
      if (this.paginationBegin + this.cardsCount < this.currencyPairs.length) {
        this.paginationBegin += this.cardsCount
        this.isDisabled.left = false
      }
      if (
        this.paginationBegin + this.cardsCount ===
        this.currencyPairs.length
      ) {
        this.isDisabled.right = true
        e.preventDefault()
      }
    },
    previousPage(e) {
      if (this.paginationBegin - this.cardsCount === 0) {
        this.isDisabled.left = true
        e.preventDefault()
      }
      if (this.paginationBegin - this.cardsCount >= 0) {
        this.paginationBegin -= this.cardsCount
        this.isDisabled.right = false
      }
    },
    addSeparator(value) {
      let i = value.indexOf(',')
      let temp = value.split('')
      while (i > 0) {
        if (i - 3 > 0) {
          temp.splice(i - 3, 0, ' ')
          i -= 2
        }
        i--
      }
      return temp.join('')
    },
  },
}
</script>
